.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  padding: 2rem 0;
  border: 4px solid white;
  background-color: crimson;
  border-radius: 12px;
  text-align: center;
  margin: 2rem 1rem;
}
.heading {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
}
input[type="email"],
input[type="text"],
input[type="password"] {
  display: block;
  text-align: center;
  width: 40%;
  margin: 1.25rem auto;
}

input[type="submit"] {
  font-weight: bold;
  width: 20%;
}
input[type="email"]:focus,
input[type="text"]:focus,
input[type="password"]:focus {
  width: 50%;
}

input[type="submit"]:hover {
  background-color: white;
  color: crimson;
}
p {
  color: white;
  font-size: 0.95rem;
  transition: 0.25s all ease-out;
  margin-top: 1.25rem;
}
p:hover {
  text-decoration: underline;
}
@media only screen and (max-width: 992px) {
    .box{
        width: 50%;
    }
    html{
        font-size: 13px;
    }
}
@media only screen and (max-width: 768px) {
    .box{
        width: 60%;
    }
    html{
        font-size: 10px;
    }
}
@media only screen and (max-width: 600px) {
    .box{
        width: 70%;
    }
    html{
        font-size: 8px;
    }
    input[type="submit"] {
        font-weight: bold;
        width: 30%;
      }
}